<template>
  <el-card class="store-card">
    <el-form ref="form" class="store-form" label-position="left" label-width="130px" :model="bussinessData">
      <el-form-item :label="$t('store.currentState')">
        <el-select v-model="bussinessData.status">
          <el-option :label="$t('store.business')" :value="1"></el-option>
          <el-option :label="$t('store.suspend')" :value="0"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item :label="$t('store.name')" prop="shop_name">
        <el-input v-model="bussinessData.name" class="storeName" style="width: 50%"> </el-input>
      </el-form-item>
      <el-form-item class="location" :label="$t('store.location')" prop="location">
        <el-input v-model="bussinessData.location" style="width: 50%"> </el-input>
      </el-form-item>
      <el-form-item :label="$t('store.openingTime')" prop="opening_time">
        <el-time-select
          :placeholder="$t('store.openTimePlaceholder')"
          v-model="bussinessData.opening_time"
          :picker-options="{
            start: '06:00',
            step: '01:00',
            end: '24:00',
          }"
          style="width: 100%"
        >
        </el-time-select>
      </el-form-item>
      <el-form-item :label="$t('store.closingTime')" prop="closing_time">
        <el-time-select
          :placeholder="$t('store.closeTimePlaceHolder')"
          v-model="bussinessData.closing_time"
          :picker-options="{
            start: '06:00',
            step: '01:00',
            end: '24:00',
          }"
          style="width: 100%"
        >
        </el-time-select>
      </el-form-item>
      <el-form-item :label="$t('store.way')">
        <el-select v-model="diningWay" value-key="" :placeholder="$t('store.diningPlaceholder')" clearable filterable multiple style="width: 100%">
          <el-option v-for="(item, key) in diningMethod" :key="key" :label="item" :value="key"> </el-option>
        </el-select>
      </el-form-item>
      <!-- <el-form-item label="In-store announcement map"
                    class="map">
        <el-image style="width: 100px; height: 100px"
                  :src="bussinessData.announcement"></el-image>
      </el-form-item> -->
      <el-upload class="avatar-uploader" action="" :show-file-list="false" :http-request="uploadFile" :before-upload="beforeAvatarUpload">
        <div class="avatar_loading" v-show="avatarLoading"></div>
        <div v-show="!avatarLoading">
          <img :src="bussinessData.logo" class="avatar" />
          <i class="el-icon-plus avatar-uploader-icon"></i>
          <div slot="tip" class="el-upload__tip">
            {{ $t("public.picInfo") }}
          </div>
        </div>
      </el-upload>
    </el-form>
    <el-button type="primary" :loading="isLoading" class="saveBtn" @click="handleSave">{{ $t("btn.save") }}</el-button>
  </el-card>
</template>

<script>
import { mapActions, mapState } from "vuex";
import { addPhoto } from "@/util/aws.config.js";
import { endLoading } from "@/util/loading.js";
export default {
  created() {
    this.getShopInfo().then(() => {
      this.bussinessData = this.shopInfo;
      this.diningWay = JSON.parse(this.bussinessData.dinning_method);
    });
  },
  mounted() {
    this.bussinessData = this.shopInfo;
  },
  data() {
    return {
      avatarLoading: false,
      // rules: {
      //     shop_name: [{
      //         required: true,
      //         message: this.$t('store.shopNameErrMsg'),
      //         trigger: 'blur',
      //     }, ],
      // },
      isLoading: false,
      bussinessData: {},
      diningWay: [],
      diningMethod: {
        in: this.$t("store.in"),
        away: this.$t("store.away"),
      },
      way: "",
    };
  },
  computed: {
    ...mapState({
      shopId: (state) => state.user.data.role_id,
      shopInfo: (state) => state.user.loginData,
    }),
  },
  methods: {
    getShopInfo() {
      let obj = {
        shop_id: this.shopId,
      };
      return this.getShopBasicInfo(obj);
    },
    // change() {
    //   let obj = {
    //     shop_id: this.shopId,
    //     business_status: Number(this.status),
    //   };
    //   this.setBussinessStatus(obj);
    // },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg" || "image/png";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error(this.$t("error.picTypeErr"));
      }
      if (!isLt2M) {
        this.$message.error(this.$t("error.picMaxErr"));
      }
    },
    uploadFile(file) {
      this.avatarLoading = true;
      addPhoto(file).then(
        (data) => {
          this.avatarLoading = false;
          endLoading();
          this.bussinessData.logo = data.Location;
        },
        (err) => {
          this.avatarLoading = false;
          endLoading();
          if (!err.msg) {
            this.$message.error(this.$t("error.picUploadErr"));
          }
        }
      );
    },
    handleSave() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.isLoading = true;
          let obj = {
            ...this.bussinessData,
            dinning_method: this.diningWay,
          };
          this.alterInfo(obj).then(
            () => {
              this.isLoading = false;
              this.$message({
                message: this.$t("store.successInfo"),
                type: "success",
              });
            },
            () => {
              this.isLoading = false;
              this.$message.error(this.$t("error.serverErr"));
            }
          );
        }
      });
    },
    ...mapActions({
      //   setBussinessStatus: "user/setBussinessStatus",
      saveInfo: "user/saveStoreInformation",
      alterInfo: "user/alterBussiness",
      uploadLogo: "user/setStorePic",
      getShopBasicInfo: "user/getShopBasicInfo",
    }),
  },
};
</script>

<style lang="scss" scoped>
.store-card {
  // max-height: calc(100vh - 8em);
  height: calc(100vh - 8em);
  overflow-y: auto;
}

.store-form {
  padding: 3em 8em 0em 8em;
}

.storeName .el-input-group__prepend {
  width: 150px;
  background-color: #fff !important;
}

.saveBtn {
  width: 12em;
  margin: 0 40%;
}

.map {
  border: 1px solid hsl(0, 0%, 85%);
}

.store-form {
  position: relative;
}

.avatar-uploader {
  position: absolute;
  top: 3em;
  right: 8em;
}

.avatar_loading {
  height: 168px;
  width: 168px;
}

.avatar-uploader .el-upload {
  border: 1px dashed hsl(0, 0%, 85%);
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}

.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 168px;
  height: 168px;
  line-height: 168px;
  text-align: center;
  position: absolute;
  top: 0;
  left: 0;
}

.avatar {
  width: 168px;
  height: 168px;
  display: block;
}

@media screen and (max-width: 1400px) and (min-width: 1330px) {
  .store-form {
    padding: 3em 4em 0 4em;
  }
}

@media screen and (max-width: 1330px) {
  .store-form {
    padding: 3em 4em 0 0;
  }
}
</style>
